import { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import {
  MdAccountTree,
  MdDescription,
  MdSpellcheck,
  MdStorage,
} from "react-icons/md";
import { BiRupee } from "react-icons/bi";

import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  clearErrors,
  createNewProduct,
} from "../../../../redux/actions/productAction";
import { NEW_PRODUCT_RESET } from "../../../../redux/constants/productConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import Loader from "../../../layout/Loader/Loader";
import H1 from "../../../ReusableComponent/Heading/H1/H1";
import Input from "../../../ReusableComponent/Inputs/Input";
const NewProduct = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { user, isAuthenticated } = useSelector((state) => state.user);

  const { loading, error, success } = useSelector((state) => state.newProduct);

  const categories = [
    "Laptop",
    "Footwear",
    "Bottom",
    "Top",
    "Attire",
    "Camera",
    "SmartPhone",
  ];
  const [newProduct, setNewProduct] = useState({
    name: "",
    price: 0,
    description: "",
    category: "",
    stock: 0,
  });

  const [images, setImages] = useState([]);
  const [imagesPreview, setImagesPreview] = useState([]);

  const createNewDataChange = (e) => {
    if (e.target.name === "images") {
      const files = Array.from(e.target.files);

      setImages([]);
      setImagesPreview([]);

      files.forEach((file) => {
        const reader = new FileReader();

        reader.onload = () => {
          if (reader.readyState === 2) {
            setImagesPreview((old) => [...old, reader.result]);
            setImages((old) => [...old, file]);
          }
        };

        reader.readAsDataURL(file);
      });
    } else {
      setNewProduct({ ...newProduct, [e.target.name]: e.target.value });
    }
  };

  const createNewProductHandler = (e) => {
    e.preventDefault();

    const myForm = new FormData();
    myForm.set("name", newProduct.name);
    myForm.set("price", newProduct.price);
    myForm.set("description", newProduct.description);
    myForm.set("category", newProduct.category);
    myForm.set("stock", newProduct.stock);

    images.forEach((img) => {
      myForm.append("images", img);
    });

    dispatch(createNewProduct(myForm));
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (success) {
      alert.success("Product Created Successfully");
      navigate("/admin/dashboard");
      dispatch({ type: NEW_PRODUCT_RESET });
    }
  }, [isAuthenticated, navigate, user, alert, dispatch, error, success]);

  return loading ? (
    <Loader />
  ) : (
    <Fragment>
      <MetaData title={`Create Product`} />

      <div className="dashboard">
        <SideBar />

        <div className="newProductContainer">
          <H1>Create Product</H1>
          <form
            className="form"
            encType="multipart/form-data"
            onSubmit={createNewProductHandler}
          >
            <Input
              icon={<MdSpellcheck />}
              type="text"
              placeholder="Product Name"
              name="name"
              value={newProduct.name}
              onChange={createNewDataChange}
            />

            <Input
              icon={<BiRupee />}
              type="number"
              name="price"
              placeholder="Price"
              required
              onChange={createNewDataChange}
            />

            <Input
              icon={<MdDescription />}
              type="textarea"
              name="description"
              placeholder="Product Description"
              value={newProduct.description}
              cols="30"
              rows="1"
              onChange={createNewDataChange}
            />

            <Input
            type="select"
              icon={<MdAccountTree />}
              value={newProduct.category}
              name="category"
              onChange={createNewDataChange}
            >
              <option value="">Select Category</option>
              {categories.map((cate, index) => (
                <option key={index} value={cate}>
                  {cate}
                </option>
              ))}
            </Input>

            <Input
              icon={<MdStorage />}
              type="number"
              name="stock"
              placeholder="Stock"
              onChange={createNewDataChange}
            />
            <Input
              type="file"
              options={{ multiple: true }}
              name="images"
              accept="image/*"
              onChange={createNewDataChange}
            >
              <div id="imagePreview">
                {imagesPreview.map((image, index) => (
                  <img key={index} src={image} alt="Avatar Preview" />
                ))}
              </div>
            </Input>

              <Input
                type="submit"
                value="Create"
                disabled={loading ? true : false}
              />
                
          </form>
        </div>
      </div>
    </Fragment>
  );
};

export default NewProduct;
